  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

div.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: auto;
}

.example {
  font-size: 14px;
  /* padding: 0 40px; */
  max-width: 1200px;
  width: 100%;
  margin: auto;
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}


.myexample {
  font-size: 14px;
  /* padding: 0 40px; */
